
import util,{_} from '@/libs/util'
import common from '@/libs/common.mixin'
import AvatarAndName from 'c%/AvatarAndName.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

import SelectClass from 'c%/SelectClass.vue'

const resName   = 'students'
const idField   = 'sid'
const textField = 'student_name'

export default{
mixins: [ common ],
components: {
Cell,
Popup,
Flexbox,
SelectClass,
FlexboxItem,
AvatarAndName
},
props: {
width: {
type: String,
default: '80%'
},
title: {
type: String,
default: ''
},
value: {
type: [String,Number,Array],
default: []
},
readonly: {
type: Boolean,
default: false
}
},
data () {
return {
popupShow: false,
model: [],
check_all: false,
data: [],
search_field: {
cid: 0,
student_name: '',
age: [0,0],
school_grade: 0,
is_assign: -1
},
search_value: ''
}
},
watch:{
popupShow: function (val) {
if(val){
this.model = util.copy(this.value)
}
},
value: function (val) {
this.model = util.copy(val)
},
model: function (val) {
this.check_all = (this.model.length === this.data.length)
},
'search_field.cid': function (val) {
this.downCallback()
}
},
methods: {
showPopup () {
if(this.readonly){
return
}
this.popupShow = true
},
getClass (row) {
return {[`active`]: this.model==row[idField]}
},
ok () {
this.$emit('input',this.model)
this.popupShow = false
},
reset () {
this.model = []
},
search () {
this.downCallback()
},
hook_get_param (params) {
let search_obj = util.copy(this.search_field)

for(let o in search_obj) {
let property = search_obj[o]
if(o=='age'){
if(parseInt(property[0])>=0
&&parseInt(property[1])>0
&&parseInt(property[1])>=parseInt(property[0])){
params.age_start = parseInt(property[0])
params.age_end = parseInt(property[1])
}
}else if(o=='bid') {
if(property!==this.bid$) {
params.bid = property
}
}else if(o=='is_assign') {
if(property==0) {
params.eid = 0
}else if(property==1) {
params.eid = '[>,0]'
}
}else{
if(property&&property!=-1){
params[o] = property
}
}
}
},
checkAll () {
this.check_all = !this.check_all

this.model = []
if(this.check_all){
this.data.forEach(b=>{
this.model.push(b[idField])
})
}

},
toggleCheck (row) {
if(this.multiple){
let index = this.model.indexOf(row[idField])
if(index>-1){
this.model.splice(index,1)
}else{
this.model.push(row[idField])
}
}else{
this.model = row[idField]
}
},
getStudentName (id) {
let data = this.data,
result = ''
if(data.length>0&&id>0){
let obj = data.find(s=>s[idField]===id)
if(obj){
result = obj[textField]
}
}
return result
}
},
computed: {
style () {
return {
background:'#f7f7f7',
width: this.width,
top: `${this.headerHeight}px`,
height: `calc(100% - ${this.headerHeight}px)`
}
},
scrollStyle () {
return {
background: '#fff',
width: this.width,
top: `${this.scrollTop}px`,
height: `calc(100% - ${this.scrollHeight}px)`
}
},
scrollTop () {
/*vux-header + x-popup-header + x-popup-search + selectClass*/
return this.headerHeight + 50 + 39 + 44
},
scrollHeight () {
/*vux-header + x-popup-header + x-popup-search + selectClass + x-popup-footer*/
return this.headerHeight + 50 + 39 + 40 +44
},
multiple(){
if(Array.isArray(this.value)){
return true
}
return false
},
labelText () {
let model = this.model,
result = ''
if(this.multiple){
if(model.length>0){
result = this.getStudentName[0]
}
if(model.length>1){
result += ''+model.length+''
}

}else{
result = this.getStudentName(model)
}
return result
},
filter_data () {
let val = this.search_value,
data = this.data
if(!/^\s*$/.test(val)){
let filter = (item) => {
return item.pinyin_abbr.indexOf(val)>-1||item.pinyin.indexOf(val)>-1||item[textField].indexOf(val)>-1
}
return data.filter(filter)
}
return data

},
data_uri () {
return 'students'
}
}
}



