import moment from 'moment'
import util from '@/libs/util'
import wxjssdk from '@/libs/wxjssdk.mixin'
import VueVideo from 'c%/vueVideo.vue' 
import SmallVoice from 'c%/SmallVoice.vue'
import PlayVideo from 'c%/PlayVideo.vue'

export default{
	mixins: [ wxjssdk ],
	components: {
		VueVideo,
		SmallVoice,
		PlayVideo,
	},
	data () {
		return {
			imgPriview:false
		}
	},
	props: {
		file: {
			type: Object,
			default: ()=>{
				return {}
			}
		}
	},
	methods: {		
		handleRemove () {
			this.$emit('handleRemove',this.file)
		},
		previewVideo (file) {
			this.$refs.playVideo
			.set('video',file)
			.show()
		},
		getDuration (item) {			
			let duration = Math.ceil(item.duration)
			return util.second_to_minute(duration)				
		},
		getFileType (item) {
			const suffixs = ['txt','doc','pdf','rar','zip','xls','ppt']
			let type = item.file_type
			if(type === 'pptx') {
				type = 'ppt'
			}
			if(type === 'docx') {
				type ='doc'
			}
			if(type === 'xlsx') {
				type = 'xls'
			}
			if(suffixs.indexOf(type) > -1) {
				return `https://sp1.xiao360.com/static/file/icon/${type}.png?imageView2/3/w/63/h/63`
			}else{
				return `https://sp1.xiao360.com/static/file/icon/default.png?imageView2/3/w/63/h/63`
			}
		},
		preview (item) { 
			this.previewImage(item.file_url,[item.file_url])
		}			
	},
	computed: {
		
	}
}



