
import modal from '@/libs/modal.mixin'
import common from '@/libs/common.mixin'
import Popup from 'vux/src/components/popup/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import Upload from 'c%/upload'

export default{
mixins: [ common, modal ],
components: {
Upload,
Popup,
XButton
},
data () {
return {
uploadList: [],
currentFileList: []
}
},
watch: {
'modal$.show': function (val) {
if(val) {
this.$nextTick(()=>{
console.log('------currentFileList')
console.log(this.$refs.handupload)

this.currentFileList = this.$refs.handupload.fileList
})
}
}
},
mounted () {

},
computed:{
style () {
return {
top: `${this.headerHeight}px`,
height: `calc(100% - ${this.headerHeight}px)`
}
}
},
methods: {
toggleUpload (item) {
if(item.status=='uploading'){
item.status = 'pause'
this.$refs.handupload.qiniuHandlePause(item)
}else{
item.status = 'uploading'
this.$refs.handupload.qiniuHandleStart(item)
}
},
del (item) {
let index = this.currentFileList.findIndex(f=>f.uid==item.uid)
if(index > -1){
this.currentFileList.splice(index, 1)
this.$refs.handupload.qiniuHandlePause(item)
}
},
getFileSize (size) {
let result = 0,
unit = 'B'

if(size / 1024 > 1024){
result = size / (1024*1024)
unit = 'M'

if(result > 1024){
result = size / (1024 * 1024 * 1024)
unit = 'G'
}

}else{
result = size / 1024
unit = 'KB'
}

result = parseFloat(result).toFixed(2)

return `${result}${unit}`
},
replaceDefaultImage(item) {
const suffixs = ['txt','doc','pdf','rar','zip','xls','ppt']
let type = item.file_type
if(type === 'pptx') {
type = 'ppt'
}
if(type === 'docx') {
type ='doc'
}
if(type === 'xlsx') {
type = 'xls'
}
if(suffixs.indexOf(type) > -1) {
return `https://sp1.xiao360.com/static/file/icon/${type}.png?imageView2/3/w/100/h/100`
}else{
return `https://sp1.xiao360.com/static/file/icon/default.png?imageView2/3/w/100/h/100`
}
},
handleSuccess (res, file) {
file.url = res.data.file_url


this.uploadList.push({
file_name: res.data.file_name,
file_size: res.data.file_size,
file_id: res.data.file_id,
file_url: res.data.file_url,
file_type: res.data.file_type,
media_type: res.data.media_type
})
this.$emit('on-success',this.uploadList)
},
}


}




