
import util from '@/libs/util'
import modal from '@/libs/modal.mixin'
import common from '@/libs/common.mixin'

import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Badge from 'vux/src/components/badge/index.vue'
import Panel from 'vux/src/components/panel/index.vue'
import InlineLoading from 'vux/src/components/inline-loading/index.vue'


export default{
mixins: [ common, modal ],
components: {
Tab,
TabItem,
Popup,
Badge,
Panel,
InlineLoading
},
data () {
return {
lesson_name: '',
rest_api: 'lesson_standard_files',
currentLesson: {},
CourseListPanel: false,
tab: 0,
isLoading: false
}
},
mounted () {
this.init()
},
methods: {
init () {
this.dr().getData('global/all',false)
.then(()=>{
this.getCenterLessons()
})
this.init_data(0)
this.init_data(1)
},
getCenterLessons () {
this.dr().getData('lessons?pagesize=10000&og_id=0&bids=-1')
.then(data=>{
if(data.list.length>0){
this.updateGvarsLessons(data.list)
}
},response=>{
this.$Notice.warning({
title: '',
desc: response.body.message||'~'
})
})
},
updateGvarsLessons (list) {
let lessons = util.copy(this.gvars('lessons'))

if(list.length > 0){
list.forEach(l=>{
if(lessons.findIndex(lesson=>lesson.lid==l.lid)==-1){
lessons.push(l)
}
})
this.$store.commit('updateGlobalVar',{name:'lessons',data:lessons})
}

},
handleClick (item) {
this.$emit('on-selected',item.files)
this.CourseListPanel = false
this.close()
},
chooseCourse (item) {
if(this.lessonStandardFileSum(item.lid) > 0){
this.currentLesson = item
this.CourseListPanel = true
}
},
lessonStandardFileSum (lid) {
let list = this.$store.state.lesson_standard_file
return list.filter(l=>l.lid==lid).length
},
init_data (val) {
let params = {
with: 'lesson_standard_file_item',
page: 1,
pagesize: 10000
},
list = this.$store.state.lesson_standard_file

if(val==1){
params.bid = -1
params.og_id = 0
}

this.isLoading = true
this.$rest(this.rest_api).get(params)
.success(data=>{
if(data.list&&data.list.length>0){
data.list.forEach(d=>{
if(list.findIndex(l=>l.lsf_id==d.lsf_id)==-1){
list.push(d)
}
})
}
this.isLoading = false
this.$store.commit('updateLessonStandardFile',list)
}).error(response=>{
this.isLoading = false
this.toast(response.body.message||'~','warn')
})
},
getImgSrc (list) {
let type = list[0].file_type,
media_type = list[0].media_type,
result = ''

if(media_type=='voice'){
result = 'https://sp1.xiao360.com/static/img/assets/file-voice.png'
}else if(media_type=='image'){
result = list[0].file_url
}else if(media_type=='video'){
result = list[0].file_url + '?vframe/jpg/offset/1/w/170/h/170)'
}else{
if(type.indexOf('doc')>-1){
result = 'w'
}
else if(type.indexOf('ppt')>-1){
result = 'p'
}
else if(type.indexOf('xls')>-1){
result = 'x'
}else{
result = 'file'
}
result = 'https://sp1.xiao360.com/static/img/assets/'+result+'.png'
}

return result
}
},
computed: {
listStyle () {
let top = 45

if(this.og_id$ > 0){
top += 46
}

if(this.isLoading){
top += 30
}

return {
height: `calc(100% - ${top}px)`,
top: `${top}px`
}
},
currentCourseList () {
let list = this.$store.state.lesson_standard_file||[],
result = []

list = list.filter(l=>l.lid==this.currentLesson.lid)

list.forEach(l=>{
let obj = {
files: l.lesson_standard_file_item,
src: '',
title: l.title,
desc: this.labelDicts(l.csft_did,'course_standard_file_type') + ''+ l.lesson_standard_file_item.length +'',
fallbackSrc: 'http://placeholder.qiniudn.com/60x60/3cc51f/ffffff',
url: ''
}
if(l.lesson_standard_file_item.length>0){
obj.src = this.getImgSrc(l.lesson_standard_file_item)
}else{
obj.src = 'https://sp1.xiao360.com/static/img/assets/file.png'
}

result.push(obj)
})

return result
},
lessons () {
let lessons = util.copy(this.gvars('lessons')),
og_id = this.tab==0?this.og_id$:0

return lessons.filter(l=>l.og_id==og_id)
},
filterLessons () {
let list = this.lessons
if(!/^\s*$/.test(this.lesson_name)){
list = list.filter(l=>l.lesson_name.indexOf(this.lesson_name)>-1)
}

return list
},
style () {
return {
zIndex: 1003,
height: `calc(100% - ${this.headerHeight}px)`,
top: this.headerHeight + 'px',
overflowX: 'hidden'
}
}
}
}




